.Portrait {
  display: inline-block;
  width: 60%;
  position: relative;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  overflow: hidden;
}

.Photo {
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
}


